import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Checkbox from '@mui/material/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel';

export default function ProjectCreate() {
    const [projectTitle, setProjectTitle] = useState('');
    const [projectDescription, setProjectDescription] = useState('');
    const [plUser, setPlUser] = useState('');
    const [devUser, setDevUser] = useState('');
    const [testUser, setTestUser] = useState('');
    const [email, setEmail] = useState('');
    const [id, setId] = useState('');
    const [modify, setModify] = useState(false);
    const navigate = useNavigate();

    const handleCreate = () => {
        const newProject = {
            projectTitle,
            projectDescription,
            plUser: plUser.split(',').map(user => user.trim()),
            devUser: devUser.split(',').map(user => user.trim()),
            testUser: testUser.split(',').map(user => user.trim())
        };
        console.log('Project !', newProject);
        //    API     .
        navigate('/');
    };

    return (
        <Paper sx={{ width: '90%', margin: 'auto', padding: 4 }}>
            <Box sx={{ display: 'flex', flexDirection: 'column', mt: 4 }}>
                <Typography variant="h6" sx={{ mb: 2, fontWeight: 'bold' }}> ()</Typography>
                <TextField
                    value="cksdn63"
                    variant="standard"
                    margin="normal"
                    fullWidth
                    InputProps={{
                        readOnly: true,
                    }}
                />

                <Typography variant="h6" sx={{ mt: 2, fontWeight: 'bold' }}> ()</Typography>
                <TextField
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    variant="standard"
                    margin="normal"
                    fullWidth
                />

                <FormControlLabel
                    control={
                        <Checkbox
                            checked={modify}
                            onChange={(e) => setModify(e.target.checked)}
                            color="primary"
                        />
                    }
                    label=""
                />

                <Typography variant="h6" sx={{ mt: 2, fontWeight: 'bold' }}>MYBOX   ID ()</Typography>
                <TextField
                    placeholder="   ID  . (   ID       .)"
                    variant="outlined"
                    margin="normal"
                    fullWidth
                />

                <Typography variant="h6" sx={{ mt: 2, fontWeight: 'bold' }}>  ID ()</Typography>
                <TextField
                    placeholder="   ID  ."
                    variant="outlined"
                    margin="normal"
                    fullWidth
                />

                <Typography variant="h6" sx={{ mt: 2, fontWeight: 'bold' }}>  ()</Typography>
                <TextField
                    placeholder="   ."
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    multiline
                    rows={4}
                />

                <Button variant="contained" color="primary" sx={{ mt: 3, backgroundColor: '#03C75A', '&:hover': { backgroundColor: '#03C75A' } }}>
                    
                </Button>
            </Box>
        </Paper>
    );
};
